  /** 重新渲染treeTable */
    initLoopCheckTable(rows){//循环检测选中状态
      for (let i = 0; i < rows.length; i++) {
        // console.info("initLoopCheckTable_i:"+i+",id:"+rows[i].id+",status:"+rows[i].status);
        if(rows[i].status==='false'){
          // this.tableRowIdMap.set(rows[i].id,false);
          this.tableRowIdMap.delete(rows[i].id );
          this.$refs.platRoleTreeTable.toggleRowSelection(rows[i],false)
        }else if(rows[i].status==='true'){
          this.tableRowIdMap.set(rows[i].id,true);

          this.$refs.platRoleTreeTable.toggleRowSelection(rows[i],true)
        }
        if(rows[i].detail){
          this.initLoopCheckTable(rows[i].detail);
        }
      }

    },

  /** 将菜单权限树初始化 将数据中 status为false的移除 */
    initTreeData(row){
      for (let i = 0; i < row.length; i++) {
        if(row[i].detail){
          this.initTreeData(row[i].detail);
        }
        if(row[i].status==='false'){
          row.splice(i, 1);
          i--;
        }
      }
      return row;
    },

  /** 清空treeTable的复选框 */
    noCheckTreeTable(rows){//循环检测选中状态
      this.tableRowIdMap=new Map();
      for (let i = 0; i < rows.length; i++) {
        this.$refs.platRoleTreeTable.toggleRowSelection(rows[i],false)
        rows[i].status = false;
        if(rows[i].detail){
          this.noCheckTreeTable(rows[i].detail);
        }
      }
    },
  /***
   * 回显使用
   * @param curId 当前选中的节点ID
   * @param sourceData tableData数据对象
   * @param targetData 新增加或修改的对象内容
   * @param layer 1是同级，2是子级
   * @param flag 将status修改成true
   *
   */
    checkPermissionId(curId,sourceData,targetData,layer,flag){
      // console.info("进入checkPermissionId:"+sourceData)
      // console.info("查询选中ID:"+curId);
      var result = false;
      for(var i1=0;i1<sourceData.length;i1++){
        //  console.info("当前id:"+sourceData[i1].id);
        if(flag==='5'){
          // console.info('查询表格status内容');
          //if(sourceData[i1].caption.toLowerCase().includes(curId.toLowerCase())){
          // console.info("query find status id:"+sourceData[i1].id);
          if(sourceData[i1].id===curId){
            sourceData[i1].status = 'true';
            //   console.info("modify status find ok");
            this.tableDataClone = this.tableData;
            //   console.info("query find status ok id:"+ sourceData[i1].id);
            result = true;
            break;
          }else{
            if(sourceData[i1].hasOwnProperty("detail")){
              //    console.info("query status not find 递归查询detail")
              result = this.checkPermissionId(curId,sourceData[i1].detail,targetData,layer,flag);
              if(result===true){
                break;
              }
            }else{
              //    console.info("query status not find");
              this.tableDataClone = [];
              result = false;

            }
          }
        }


      }
      return result;

    },

   /** 将用户权限点赋予到tableData */
    initTableDataPermissionStatus(permissionKey,tableData){
      for (let k of Object.keys(permissionKey)) {
        this.checkPermissionId(k,tableData,
          {},
          '0',
          '5');
      }
      //初始化渲染
      this.$nextTick(() => {
        this.initLoopCheckTable(tableData);
      })
    },

